$size: 100px;
$gap: 1px;
$border: 1px solid #000;

@mixin centered {
  justify-content: center;
  align-items: center;
}

@mixin mainStayl {
  display: none;
  height: $size;
  width: $size;
  box-sizing: border-box;
  margin: 0 $gap $gap 0;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 30px;
  background-color: #eee;
  border: $border;
  &::after {
    display: inline;
  }
  &:hover {
    margin-top: -20px;
    margin-bottom: 0;
    background-color: #ffbfcb;
    transition-duration: 0.5s;
    cursor: pointer;
  }
}

body {
  margin: 0;
  height: 100vh;
  @include centered();
}

.calendar {
  display: flex;
  flex-wrap: wrap;
  width: (($size+$gap)*7);
}

.calendar > div {
  @include mainStayl();
  @include centered();
}

@for $i from 1 through 31 {
  .calendar div:nth-child(#{$i})::after {
    content: "#{$i}";
  }
}

@for $i from 1 through 31 {
  .calendar-#{$i} div:nth-child(-n+#{$i}) {
    display: flex;
  }
}

$days-margins:
  "tue" 101px,
  "wed" 202px,
  "thu" 303px,
  "fri" 404px,
  "sat" 505px,
  "sun" 606px;

@each $day, $margin in $days-margins {
  .calendar-#{$day}:first-child {
    div:first-child {
      margin-left: $margin;
    }
  }
}
